.linkS{
    padding-left: 1.5rem;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    color:white;
    background-color: var(--color-bg-close);

}

.laugh{
    padding-top: 1.5rem;
    text-align: center;
    padding-bottom: 0.5rem;
}


.articleHeading{
    font-family: Verdana;
    padding-left: 1.5rem;
    color: var(--color-primary);
    font-size: 1.2rem;
    line-height: 2rem;
}

.articlesH{
    margin-top: 0.5rem;
    margin-bottom: 3rem;
    width:50%;
    margin-left: 25%;
    margin-right: 25%;
    display: grid;
    grid-auto-flow: row;
    grid-row-gap: 20px;
}


.jokesStyle{
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    background-color: var(--color-bg-close);
    margin-bottom: 5rem;
    border: 1px solid transparent;
    border-radius: 2rem;
    font-family: Verdana;
}

.jokesStyle:hover{
    background: transparent;
    border: 1px solid transparent;
    border-color: var(--color-primary-variant);
}

.jokeTitle{
    padding-left: 1.5rem;
    padding-bottom: 1rem;
    color: var(--color-primary);
}

.jokeBody{
    padding-left: 1.5rem;
    padding-right: 1rem;
    display: grid;
    grid-auto-flow: row;
    grid-row-gap: 1.5rem;
}

.imageStyleJ{
    margin:auto;
    margin-top: 1.5rem;
    padding-top: 0.5rem;
    margin-bottom: 5rem;
    padding-bottom: 0.5rem;
    width:18.75rem;
    height:28.125rem;
    object-fit: contain;
}

.jokes{
    width:50%;
    margin-left: 25%;
    margin-right: 25%;
}

.scroll__up {
    position: fixed;
    right: 0.1px;
    bottom: 2rem;
    transform: rotate(90deg);
    font-weight: bold;
    font-size: .9rem;
}

/* =============== MEDIA QUERIES (MEDIUM DEVICES) ============== */
@media screen and (max-width: 1024px) {

    .jokes{
        width:70%;
        margin-left: 15%;
        margin-right: 15%;
    }

    .articlesH{
        width:90%;
        margin-left: 5%;
        margin-right: 5%;
        grid-row-gap: 15px;
    }

    .scroll__up {
        font-weight: 200;
        font-size: .7rem;
    }
}

/* =============== MEDIA QUERIES (SMALL DEVICES) ============== */
@media screen and (max-width: 600px) {
  
    .laugh>h1{
        font-size: 2rem;       
    }

    .jokes{
        width:96%;
        margin-left: 2%;
        margin-right: 2%;
    }

    .jokeTitle>h2{
        font-size: 1.5rem;  
    }
    
    .jokeBody>h3{
        font-size: 1.08rem;
    }

    .articlesH{
        width:98%;
        margin-left: 1%;
        margin-right: 1%;
        grid-row-gap: 12px;
    }
}
