.samePosEng {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    background-color: black;
    width: 30%;
    margin-top: .5%;
    margin-left: 35%;
    margin-right: 35%;
    color: gray;
    font-size: larger;
    font-weight: bold;
    cursor: pointer;
    text-align: center;
}

.restart1Eng {
    color: gray;

}

.restart2Eng {
    color: gray;

}

.exitEng {
    color: gray;

}

.sentenceBG {
    background-color: rgb(10, 10, 34);
    padding-top: 30px;
    padding-bottom: 40px;
    width: 30%;
    margin-left: 35%;
    margin-right: 35%;
}

.score {
    text-align: center;
    margin-bottom: 10px;
    color: rgb(240, 240, 156);
}

.sentence {
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 15px;
    color: rgb(241, 210, 210);
    cursor: pointer;
}

.mainSentence {
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 15px;
    color: rgb(183, 224, 243);
}

.sentence2 {
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 15px;
    color: rgb(183, 224, 243);
    cursor: pointer;
}

.correct {
    color: rgb(88, 177, 88);
}

.wrong {
    color: rgb(247, 84, 84);
}

.button-container {
    text-align: center;

}

#nextBtn {

    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 30px;
    font-size: larger;
    font-weight: bold;
}

.finalWord {
    text-align: center;
    margin-top: 30px;
    color: rgb(240, 240, 156);
}

.sentence::before {
    content: "●";
    /* Filled circle character */
    margin-right: 5px;
    /* Adjust spacing between bullet and sentence */
}

.sentence2::before {
    content: "●";
    /* Filled circle character */
    margin-right: 5px;
    /* Adjust spacing between bullet and sentence */
}

.correct::before {
    color: green;
    /* Change color of bullet for correct sentences */
}

.wrong::before {
    color: red;
    /* Change color of bullet for wrong sentences */
}

.tooltip-textEng {
    visibility: hidden;
    position: absolute;
    z-index: 1;
    width: 70%;
    color: rgb(22, 41, 56);
    font-size: 14px;
    background-color: white;
    border-radius: 5px;
    padding: 5px;
    top: 25px;
    text-align: left;
}

.tooltip-text-desktopEng {
    visibility: hidden;
    position: absolute;
    z-index: 1;
    width: 20%;
    color: rgb(22, 41, 56);
    font-size: 20px;
    background-color: white;
    border-radius: 5px;
    padding: 5px;
    top: 25px;
    text-align: left;
}

@media screen and (min-width: 1300px) {

    .infoEng:hover .tooltip-text-desktopEng {
        visibility: visible;
    }
}

/* =============== MEDIA QUERIES (MEDIUM DEVICES) ============== */
@media screen and (max-width: 1280px) {

    .samePosEng {
        position: relative;
        top: 0;
        width: 100%;
        margin: auto;
        padding: 10px;
        text-align: center;
    }

    .sentenceBG {
        width: 100%;
        margin: auto;
    }


    .infoEng:hover .tooltip-textEng {
        visibility: visible;
    }

    .infoEng .tooltip-textEng:hover {
        display: none;
    }

    .sentence, .sentence2 {
        cursor: none;
    }


}