.full-screen-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8); /* Dark background with transparency */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Ensure it's on top of other elements */
}

.popup-content {
  background: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

img {
  max-width: 100%;
  max-height: 80vh; /* Ensure the image doesn't overflow */
}

.close-button {
  margin-top: 10px;
  padding: 10px 20px;
  border: none;
  background: #0a2131; /* Blue button color */
  color: white;
  cursor: pointer;
  border-radius: 5px;
}

.close-button:hover {
  background: #2980b9; /* Darker blue on hover */
}
